.tooltip {
    position:absolute;
}

.tooltip .top {
    min-width:200px;
    max-width:200px;
    height: 40px;
    background-color: green;
    font-weight:normal;
    font-size:14px;
    border-radius:8px;
    position: relative;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    -webkit-box-shadow:0 1px 8px rgba(0,0,0,0.5);
            box-shadow:0 1px 8px rgba(0,0,0,0.5);
}


.tooltip .top i {
    position:absolute;
    top:100%;
    left:40%;
    width:30px;
    height:15px;
    overflow:hidden;
}

.tooltip .top i::after {
    content:'';
    position:absolute;
    width:15px;
    height:15px;
    left:50%;
    -webkit-transform:translate(-50%,-50%) rotate(45deg);
        -ms-transform:translate(-50%,-50%) rotate(45deg);
            transform:translate(-50%,-50%) rotate(45deg);
    background-color:green;
    -webkit-box-shadow:0 1px 8px rgba(0,0,0,0.5);
            box-shadow:0 1px 8px rgba(0,0,0,0.5);
}





